import { createGetterSetter } from '../../../src/plugins/opensearch_dashboards_utils/public';
// Maps Legacy Related
let toast;
export const setToasts = notificationToast => toast = notificationToast;
export const getToasts = () => toast;
let uiSettings;
export const setUiSettings = coreUiSettings => uiSettings = coreUiSettings;
export const getUiSettings = () => uiSettings;
let opensearchDashboards;
export const setOpenSearchDashboardsVersion = version => opensearchDashboards = version;
export const getOpenSearchDashboardsVersion = () => opensearchDashboards;
let mapsExplorerDashboardsConfig;
export const setMapsExplorerDashboardsConfig = config => mapsExplorerDashboardsConfig = config;
export const getMapsExplorerDashboardsConfig = () => mapsExplorerDashboardsConfig;
export const getEmsTileLayerId = () => getMapsExplorerDashboardsConfig().emsTileLayerId; // Tile Map Related

export const [getCoreService, setCoreService] = createGetterSetter('Core');
export const [getFormatService, setFormatService] = createGetterSetter('data.fieldFormats');
export const [getNotifications, setNotifications] = createGetterSetter('Notifications');
export const [getQueryService, setQueryService] = createGetterSetter('Query');
export const [getShareService, setShareService] = createGetterSetter('Share');
export const [getOpenSearchDashboardsLegacy, setOpenSearchDashboardsLegacy] = createGetterSetter('OpenSearchDashboardsLegacy');